import "core-js/modules/es.array.push.js";
import router from '@/router';
import { reactive, toRefs } from '@vue/reactivity';
export default {
  setup() {
    let arry = reactive({
      down: []
    });
    let back = () => {
      router.push({
        path: "/my"
      });
    };
    arry.down = JSON.parse(window.localStorage.getItem("readydown"));
    console.log(arry.down);
    let del = id => {
      console.log(id);
      let Index = arry.down.findIndex(item => item.id == id);
      console.log(Index);
      if (Index != -1) {
        arry.down.splice(Index, 1);
        window.localStorage.setItem("readydown", JSON.stringify(arry.down));
      }
    };
    let govideo = id => {
      let index = arry.down.findIndex(item => {
        return item.id == id;
      });
      console.log(index);
      //点击跳转后传index值
      router.push({
        path: "/television",
        query: {
          "id": index
        }
      });
    };
    return {
      back,
      ...toRefs(arry),
      del,
      govideo
    };
  }
};